img {
    border: none;
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 0.5cm;
}

.loader {
    width: 48px;
    height: 48px;
    border: 5px solid #000000;
    border-bottom-color: transparent;
    border-radius: 50%;
    display: inline-block;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
}

.member {

    display: flex;

    flex-direction: column;
    padding: .2cm .3cm;
    background-color: rgb(225, 225, 225);
    row-gap: .2cm;
}

.members-head {
    margin-bottom: .1cm;
}

.members-list {
    display: flex;
    flex-direction: column;
    row-gap: .1cm;
}

.game-page {
    display: flex;
    margin-top: 2CM;
    align-items: flex-start;
    column-gap: 3cm;
}

.members {
    width: 8cm;

}

@keyframes rotation {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.main {
    font-size: 50px;
    font-weight: bold;
    background: linear-gradient(to right, rgb(247, 85, 255), rgb(58, 150, 255));
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    margin: 0;
    margin-top: 0.5cm;
}

.game {
    display: flex;
    flex-direction: column;
    row-gap: 0.5cm;
}

.row1,
.row2,
.row3 {
    display: flex;
    flex-direction: row;
    column-gap: 0.5cm;
    row-gap: 0.5cm;
}

.b1 {
    height: 1cm;
    width: 1cm;
    padding: 0.5cm;
    border-radius: 4px;
    border: none;
    background-color: rgb(255, 255, 255);
    box-shadow: 0.5px 0.5px 2px 2px rgb(98, 98, 98);
    transition: 2s ease;
}

.heading {
    font-size: 24px;
    color: rgb(132, 53, 206);
    text-shadow: 1px 1px plum;
    font-weight: bolder;
}

.resultp {
    font-size: 20px;
    color: rgb(114, 4, 4);
    font-weight: bold;
}

.restartButton {
    width: 1cm;
    height: 1cm;
}

@media only screen and (max-width:400px) {
    .b1 {
        height: 0.8cm;
        width: 0.8cm;
        padding: 0.2cm;
    }

    .main {
        font-size: 35px;
    }
}